const tabs = ({
    tabsParentSelector,
    itemsParentSelector,
    activeStyle,
    displayStyle = 'block'
    }) => {
    const items = [...document.querySelector(itemsParentSelector).children],
          tabs = [...document.querySelector(tabsParentSelector).children];

        function hideTabContent() {
        
            items.forEach(item => {
                item.style.display = 'none'
            });
    
            tabs.forEach(item => {
                item.classList.remove(activeStyle);
            });
        }
    
        function showTabContent(i = 0) {
            // items[i].classList.add('show', 'fade');
            // items[i].classList.remove('hide');
            items[i].style.display = displayStyle;
            tabs[i].classList.add(activeStyle);
        }
        
        hideTabContent();
        showTabContent();
        tabs.forEach(tab => {
            tab.addEventListener('click', function(e) {
            
                tabs.forEach((item, i) => {
                    if (e.target == item) {
                        hideTabContent();
                        showTabContent(i);
                    }
                
                });
        });
        })
}